import firebase from 'firebase';
import { firestorePlugin} from "vuefire";
import Vue from "vue";
import  "firebase/auth";
import "firebase/firestore";
Vue.use(firestorePlugin)

export default {
  name: 'logout',

  created() {

    firebase.auth().signOut();
    this.$store.dispatch("auth/logOut");
  },

};
